*{
  margin: 0;
  padding: 0;
}



.background{
  background: rgba(0,0, 0, 0.09) url("bg4.jpg");
  background-size: cover;
  background-blend-mode: darken;
 
}

.navbar{
  display: flex;
  align-items: center;
  justify-content: center;
  position:sticky;
  top: 0;
  height: 60px;
  cursor: pointer;
  color: white;
  font-size: 24px;
  background-color:rgb(30, 48, 48) ;
}



.nav-list{
   display: flex;
   width: 50vw;
   align-items: center;
   justify-content: center;
   margin-left: 17px;
  
   
   
}
.nav-list li{
   list-style: none;
   padding: 33px;
   
}
.rightnav{
  
  display: flex;
  align-items: center;
  justify-content: end;
  width: 50vw;
}
.rightnav li{
  list-style: none;
  padding: 33px;
  margin-right: 80px;
  
}



